import React, { Component } from 'react';
import './App.css';
import Button from '@material-ui/core/Button';
import Grid from '@material-ui//core/Grid';

class App extends Component {
  state = {
    redHead: 0,
    redBody: 0,
    redFoulCount: 0,
    blueHead: 0,
    blueBody: 0,
    blueFoulCount: 0,
    show: true
  }
  IncrementRedHead = () => this.setState({ redHead: this.state.redHead +3 });
  IncrementRedBody = () => this.setState({ redBody: this.state.redBody +2 });
  IncrementRedFoulCount = () => this.setState({ redFoulCount: this.state.redFoulCount -1 });
  IncrementBlueHead = () => this.setState({ blueHead: this.state.blueHead +3 });
  IncrementBlueBody = () => this.setState({ blueBody: this.state.blueBody +2});
  IncrementBlueFoulCount = () => this.setState({ blueFoulCount: this.state.blueFoulCount -1 });
  ResetAll = () => this.setState({
    redHead: 0,
    redBody: 0,
    redFoulCount: 0,
    blueHead: 0,
    blueBody: 0,
    blueFoulCount: 0,
    show: true
  });
  render() {
    const blueStyle = {
      backgroundColor: 'blue',
      color: 'white',
      fontWeight: 'bold',
      width: '80%'
    }
    const redStyle = {
      backgroundColor: 'red',
      color: 'white',
      fontWeight: 'bold',
      width: '80%'
    }
    const titleStyle = {
      width: '100%'
    }
    return (
      <div className="App">
        <header className="App-header">
          <h1 className="App-title">Taekwondo Scoreboard App</h1>
        </header><br />
        <Grid container spacing={24}>
          <Grid item xs={6} >
            <div className="Red-Score" style={titleStyle} >
              {this.state.show ? <h1>Red Score<br/> {this.state.redFoulCount + this.state.redBody + this.state.redHead}</h1>: '' }
            </div> <br/>
            <Button 
              style={redStyle}
              onClick={this.IncrementRedHead}>Red Headshot: {this.state.redHead} </Button> <br/><br/>       
            <Button 
              style={redStyle}
              onClick={this.IncrementRedBody}>Red Bodyshot: {this.state.redBody} </Button><br/><br/> 
            <Button 
              style={redStyle}
              onClick={this.IncrementRedFoulCount}>Red Foul: {this.state.redFoulCount} </Button> 
          </Grid>
          <Grid item xs={6} >
            <div className="Blue-Score" style={titleStyle} >
             {this.state.show ? <h1>Blue Score<br/> {this.state.blueFoulCount + this.state.blueBody + this.state.blueHead}</h1>: '' }
            </div><br/>
            <Button 
             style={blueStyle}
             onClick={this.IncrementBlueHead}>Blue Headshot: {this.state.blueHead} </Button> <br/><br/>
            <Button 
             style={blueStyle}
              onClick={this.IncrementBlueBody}>Blue Bodyshot: {this.state.blueBody} </Button> <br/><br/>
            <Button 
              style={blueStyle}
              onClick={this.IncrementBlueFoulCount}>Blue Foul: {this.state.blueFoulCount} </Button> 
          </Grid>
          <Grid item xs={12} >
            <Button 
              variant="contained" 
              color="gray"
              onClick={this.ResetAll}>Reset</Button>
          </Grid>
        </Grid>
      </div>
    );
  }
}
export default App;


